import PricingCampaignApp from "../templates/pricing-campaign-panel";
import { socket, joinSingleChannel } from "./socket"

 Vue.component('pricing-campaign-panel', PricingCampaignApp)

 class PricingCampaignPanel {
   setup() {
    this.setupComponent();
    this.loaded = false;
  }
   setupComponent() {
    const capturePeriod = document.getElementById("capture-period").value;
    const channelHash = document.getElementById("channel-hash").value;
    joinSingleChannel(`pricing_campaign:data:${channelHash}`, "panel", (channel_type, channel, params) => {
      window.PricingCampaignChannel = channel;
      console.log("channel joined. loaded: " + this.loaded);
      if (!this.loaded) {
        this.loaded = true;
        window.vueApp = new Vue({
          el: '#app',
          mounted() {
            console.log("app ready");
          },
        });
      }
    }, { capturePeriod });

  }
}

window.pricingCampaignPanel = new PricingCampaignPanel();
