import SearchShowsApp from "../templates/search-shows-panel";
import { socket, joinSingleChannel } from "./socket"

 Vue.component('search-shows-panel', SearchShowsApp)

 class SearchShowsPanel {
   setup() {
    this.setupComponent();
    this.loaded = false;
  }
   setupComponent() {
    const capturePeriod = document.getElementById("capture-period").value;
    const channelHash = document.getElementById("channel-hash").value;
    joinSingleChannel(`search_shows:data:${channelHash}`, "panel", (channel_type, channel, params) => {
      window.SearchShowsChannel = channel;
      console.log("channel joined. loaded: " + this.loaded);
      if (!this.loaded) {
        this.loaded = true;
        window.vueApp = new Vue({
          el: '#app',
          mounted() {
            console.log("app ready");
          },
        });
      }
    }, { capturePeriod });

  }
}

window.searchShowsPanel = new SearchShowsPanel();
